e;\n    position: fixed;\n    left: calc(25vw + 20px);\n    bottom: 20px;\n    display: flex;\n    justify-content: center;\n    align-items: center;\n}\n\n.add-window {\n    position: absolute;\n    height: 30vh;\n    width: 70vw;\n    background-color: white;\n    left: 0;\n    right: 0;\n    margin-left: auto;\n    margin-right: auto;\n    top: 30vh;\n    border: 4px lightblue solid;\n    border-radius: 20px;\n    box-shadow: 0px 0px 10px gray;\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    justify-content: space-around;\n}\n\n.add-window div {\n    display: flex;\n    flex-direction: column;\n}\n\n.add-window div.buttons {\n    flex-direction: row;\n    gap: 20px;\n}\n\n.buttons button {\n    height: 50px;\n    width: 100px;\n    border: 1px darkgray solid;\n    box-shadow: 2px 2px 5px lightgray;\n    border-radius: 5px;\n}\n\n.buttons button:first-child{\n    background-color: #14F500;\n}\n\n.buttons button:last-child {\n    background-color: #F55A4B;\n}", "",{"version":3,"sources":["webpack://./src/style.css"],"names":[],"mappings":"AAAA;IACI,SAAS;IACT,UAAU;IACV,sBAAsB;IACtB,yCAAyC;AAC7C;;AAEA;IACI,aAAa;IACb,YAAY;IACZ,aAAa;IACb,8BAA8B;IAC9B,2BAA2B;AAC/B;;AAEA;IACI,mBAAmB;IACnB,eAAe;IACf,2BAA2B;IAC3B,aAAa;IACb,uBAAuB;IACvB,mBAAmB;IACnB,eAAe;IACf,yCAAyC;AAC7C;;AAEA;IACI,kBAAkB;IAClB,eAAe;IACf,sBAAsB;IACtB,aAAa;IACb,gCAAgC;IAChC,uBAAuB;IACvB,mBAAmB;IACnB,aAAa;AACjB;;AAEA;IACI,eAAe;AACnB;;AAEA;IACI,kBAAkB;IAClB,eAAe;IACf,gBAAgB;AACpB;;AAEA;IACI,aAAa;IACb,YAAY;IACZ,kBAAkB;IAClB,uBAAuB;IACvB,oBAAoB;IACpB,YAAY;IACZ,eAAe;IACf,iBAAiB;IACjB,oBAAoB;IACpB,aAAa;IACb,uBAAuB;IACvB,mBAAmB;AACvB;;AAEA;IACI,mBAAmB;IACnB,eAAe;IACf,2BAA2B;IAC3B,aAAa;IACb,qBAAqB;IACrB,2DAA2D;IAC3D,mBAAmB;IACnB,uBAAuB;IACvB,SAAS;IACT,aAAa;IACb,gBAAgB;AACpB;;AAEA;IACI,aAAa;IACb,aAAa;IACb,sBAAsB;IACtB,8BAA8B;IAC9B,iBAAiB;IACjB,mBAAmB;IACnB,uBAAuB;AAC3B;;AAEA;IACI,WAAW;IACX,YAAY;AAChB;;AAEA;IACI,aAAa;IACb,YAAY;IACZ,YAAY;IACZ,kBAAkB;IAClB,2BAA2B;IAC3B,eAAe;IACf,YAAY;IAC